import { BaseTrack, TrackConfig, TrackData } from '../base/base-track';
import { MSASequence } from '../../types';

export class MSATrack extends BaseTrack {

  constructor(config: TrackConfig, data: TrackData) {
    super('msa', config, data);
  }

  createHTML(): string {
    if (!this.hasRequiredData()) {
      return '';
    }

    // For scrollbox, we'll create the MSA track directly and let the viewer handle scrollbox wrapping
    return `
      <nightingale-msa
        id="msa-track"
        style="width: 100%; display: block;"
        height="${this.config.height}"
        color-scheme="conservation"
        length="${this.config.sequenceLength}"
        display-start="${this.config.displayStart}"
        display-end="${this.config.displayEnd}"
        label-width="${this.config.labelWidth}"
        label-font-size="16"
        label-font-weight="600"
        use-ctrl-to-zoom>
      </nightingale-msa>
    `;
  }

  setup(container: HTMLElement): void {
    this.element = container.querySelector('#msa-track') as HTMLElement;
    
    if (!this.element) {
      console.error('MSA track element not found');
      return;
    }

    this.setupMSATrack();
  }

  update(config?: Partial<TrackConfig>, data?: Partial<TrackData>): void {
    if (config) {
      this.config = { ...this.config, ...config };
    }
    if (data) {
      this.data = { ...this.data, ...data };
    }
    
    if (this.element) {
      this.setupMSATrack();
    }
  }

  destroy(): void {
    this.element = undefined;
  }


  private setupMSATrack(): void {
    if (!this.element) return;

    const nightingaleAttributes = this.getNightingaleAttributes();
    this.applyAttributes(this.element, {
      ...nightingaleAttributes,
      'label-width': this.config.labelWidth.toString(),
      'label-font-size': '16',
      'label-font-weight': '600',
      'use-ctrl-to-zoom': 'true'
    });

    // Ensure the element is visible
    this.element.style.display = 'block';
    this.element.style.width = '100%';
  }
}